import { 
  collection, 
  doc, 
  getDocs, 
  getDoc, 
  addDoc, 
  updateDoc, 
  deleteDoc, 
  query, 
  where, 
  orderBy,
  serverTimestamp,
  writeBatch
} from 'firebase/firestore';
import { db } from '../firebase';

export interface DiscountData {
  id?: string;
  name: string;
  discount_type: 'percent' | 'fixed';
  discount_amount: number;
  start_date: Date;
  end_date: Date;
  isActive: boolean;
  motor_types?: string[]; // Array of motor type IDs
  image_url?: string; // Image URL for discount banner
  createdAt?: Date;
  updatedAt?: Date;
  createdBy?: string;
  updatedBy?: string;
}

export interface DiscountFormData {
  name: string;
  discount_type: 'percent' | 'fixed';
  discount_amount: number;
  start_date: string;
  end_date: string;
  isActive: boolean;
  motor_types: string[];
  image_url?: string;
}

const COLLECTION_NAME = 'discount_list';

export class DiscountService {
  // Get all discounts
  static async getDiscounts(): Promise<DiscountData[]> {
    try {
      // Simple query without orderBy to avoid index issues
      const querySnapshot = await getDocs(collection(db, COLLECTION_NAME));
      const discounts: DiscountData[] = [];
      
      console.log('📊 Fetching discounts from collection:', COLLECTION_NAME);
      console.log('📊 Found documents:', querySnapshot.size);
      
      querySnapshot.forEach((doc) => {
        const data = doc.data();
        console.log('📊 Document data:', doc.id, data);
        
        discounts.push({
          id: doc.id,
          ...data,
          start_date: data.start_date?.toDate(),
          end_date: data.end_date?.toDate(),
          createdAt: data.createdAt?.toDate(),
          updatedAt: data.updatedAt?.toDate(),
        } as DiscountData);
      });
      console.log('discountsdiscounts1', discounts)
      
      // Sort by name or start_date on client side
      discounts.sort((a, b) => {
        if (a.createdAt && b.createdAt) {
          return b.createdAt.getTime() - a.createdAt.getTime();
        }
        return a.name.localeCompare(b.name);
      });
      console.log('discountsdiscounts2', discounts)
      
      console.log('📊 Processed discounts:', discounts);
      return discounts;
    } catch (error) {
      console.error('Error getting discounts:', error);
      throw new Error('Failed to fetch discounts');
    }
  }

  // Get active discount
  static async getActiveDiscount(): Promise<DiscountData | null> {
    try {
      const now = new Date();
      
      // Get ALL discounts first to avoid any query complexity
      const querySnapshot = await getDocs(collection(db, COLLECTION_NAME));
      
      if (querySnapshot.empty) {
        return null;
      }

      // Process all discounts and find active ones
      const activeDiscounts: DiscountData[] = [];
      querySnapshot.forEach((doc) => {
        const data = doc.data();
        const startDate = data.start_date?.toDate();
        const endDate = data.end_date?.toDate();
        
        if (data.isActive && startDate && endDate && startDate <= now && endDate >= now) {
          activeDiscounts.push({
            id: doc.id,
            ...data,
            start_date: startDate,
            end_date: endDate,
            createdAt: data.createdAt?.toDate(),
            updatedAt: data.updatedAt?.toDate(),
          } as DiscountData);
        }
      });

      // Return the first valid active discount
      return activeDiscounts.length > 0 ? activeDiscounts[0] : null;
    } catch (error) {
      console.error('Error getting active discount:', error);
      throw new Error('Failed to fetch active discount');
    }
  }

  // Get active discounts for dropdown (including future ones)
  static async getActiveDiscountsForDropdown(): Promise<DiscountData[]> {
    try {
      const now = new Date();
      
      // Get ALL discounts to avoid query complexity
      const querySnapshot = await getDocs(collection(db, COLLECTION_NAME));
      const discounts: DiscountData[] = [];
      
      querySnapshot.forEach((doc) => {
        const data = doc.data();
        const startDate = data.start_date?.toDate();
        const endDate = data.end_date?.toDate();
        
        // Filter: must be active and not expired
        if (data.isActive && endDate && endDate >= now) {
          discounts.push({
            id: doc.id,
            ...data,
            start_date: startDate,
            end_date: endDate,
            createdAt: data.createdAt?.toDate(),
            updatedAt: data.updatedAt?.toDate(),
          } as DiscountData);
        }
      });
      
      // Sort by end date
      return discounts.sort((a, b) => a.end_date.getTime() - b.end_date.getTime());
    } catch (error) {
      console.error('Error getting active discounts for dropdown:', error);
      throw new Error('Failed to fetch active discounts');
    }
  }

  // Get single discount
  static async getDiscount(id: string): Promise<DiscountData | null> {
    try {
      const docRef = doc(db, COLLECTION_NAME, id);
      const docSnap = await getDoc(docRef);
      
      if (docSnap.exists()) {
        const data = docSnap.data();
        return {
          id: docSnap.id,
          ...data,
          start_date: data.start_date?.toDate(),
          end_date: data.end_date?.toDate(),
          createdAt: data.createdAt?.toDate(),
          updatedAt: data.updatedAt?.toDate(),
        } as DiscountData;
      }
      
      return null;
    } catch (error) {
      console.error('Error getting discount:', error);
      throw new Error('Failed to fetch discount');
    }
  }

  // Create discount
  static async createDiscount(discountData: DiscountFormData, createdBy?: string): Promise<string> {
    try {
      console.log('🚀 Creating discount with data:', discountData);
      
      // Use addDoc instead of batch for simpler creation
      const newDiscountData = {
        name: discountData.name,
        discount_type: discountData.discount_type,
        discount_amount: discountData.discount_amount,
        start_date: new Date(discountData.start_date),
        end_date: new Date(discountData.end_date),
        isActive: discountData.isActive,
        motor_types: discountData.motor_types || [],
        image_url: discountData.image_url || '',
        createdAt: serverTimestamp(),
        createdBy: createdBy || '',
        updatedAt: serverTimestamp(),
        updatedBy: createdBy || ''
      };

      console.log('📊 Creating document with data:', newDiscountData);
      const docRef = await addDoc(collection(db, COLLECTION_NAME), newDiscountData);
      console.log('✅ Document created with ID:', docRef.id);
      
      // If this discount is being set as active, deactivate all other discounts
      if (discountData.isActive) {
        console.log('🔄 Deactivating other active discounts...');
        const activeDiscountsQuery = query(
          collection(db, COLLECTION_NAME),
          where('isActive', '==', true)
        );
        const activeDiscountsSnapshot = await getDocs(activeDiscountsQuery);
        
        const batch = writeBatch(db);
        activeDiscountsSnapshot.forEach((discountDoc) => {
          // Don't deactivate the newly created discount
          if (discountDoc.id !== docRef.id) {
            const discountRef = doc(db, COLLECTION_NAME, discountDoc.id);
            batch.update(discountRef, {
              isActive: false,
              updatedAt: serverTimestamp(),
              updatedBy: createdBy || ''
            });
          }
        });
        
        if (activeDiscountsSnapshot.size > 1) {
          await batch.commit();
          console.log('✅ Other active discounts deactivated');
        }
      }
      
      return docRef.id;
    } catch (error) {
      console.error('❌ Error creating discount:', error);
      console.error('Error details:', error);
      throw new Error('Failed to create discount');
    }
  }

  // Update discount
  static async updateDiscount(id: string, discountData: DiscountFormData, updatedBy?: string): Promise<void> {
    try {
      const batch = writeBatch(db);
      
      // If this discount is being set as active, deactivate all other discounts
      if (discountData.isActive) {
        const activeDiscountsQuery = query(
          collection(db, COLLECTION_NAME),
          where('isActive', '==', true)
        );
        const activeDiscountsSnapshot = await getDocs(activeDiscountsQuery);
        
        activeDiscountsSnapshot.forEach((discountDoc) => {
          // Don't deactivate the current discount being updated
          if (discountDoc.id !== id) {
            const discountRef = doc(db, COLLECTION_NAME, discountDoc.id);
            batch.update(discountRef, {
              isActive: false,
              updatedAt: serverTimestamp(),
              updatedBy: updatedBy || ''
            });
          }
        });
      }

      // Update the discount
      const discountRef = doc(db, COLLECTION_NAME, id);
      const updateData = {
        name: discountData.name,
        discount_type: discountData.discount_type,
        discount_amount: discountData.discount_amount,
        start_date: new Date(discountData.start_date),
        end_date: new Date(discountData.end_date),
        isActive: discountData.isActive,
        motor_types: discountData.motor_types || [],
        image_url: discountData.image_url || '',
        updatedAt: serverTimestamp(),
        updatedBy: updatedBy || ''
      };

      batch.update(discountRef, updateData);
      
      // Commit the batch
      await batch.commit();
    } catch (error) {
      console.error('Error updating discount:', error);
      throw new Error('Failed to update discount');
    }
  }

  // Delete discount
  static async deleteDiscount(id: string): Promise<void> {
    try {
      const docRef = doc(db, COLLECTION_NAME, id);
      await deleteDoc(docRef);
    } catch (error) {
      console.error('Error deleting discount:', error);
      throw new Error('Failed to delete discount');
    }
  }

  // Calculate discount amount
  static calculateDiscountAmount(originalPrice: number, discount: DiscountData): number {
    if (discount.discount_type === 'percent') {
      return Math.round(originalPrice * (discount.discount_amount / 100));
    } else {
      return discount.discount_amount;
    }
  }

  // Calculate final price after discount
  static calculateDiscountedPrice(originalPrice: number, discount: DiscountData): number {
    const discountAmount = this.calculateDiscountAmount(originalPrice, discount);
    return Math.max(0, originalPrice - discountAmount);
  }

  // Check if discount is currently valid
  static isDiscountValid(discount: DiscountData): boolean {
    const now = new Date();
    return (
      discount.isActive &&
      discount.start_date <= now &&
      discount.end_date >= now
    );
  }

  // Check if discount applies to specific motor type
  static doesDiscountApplyToMotorType(discount: DiscountData, motorTypeId: string): boolean {
    if (!discount.motor_types || discount.motor_types.length === 0) {
      return true; // If no specific motor types, applies to all
    }
    return discount.motor_types.includes(motorTypeId);
  }
}
