'use client';

import { useState, useEffect } from 'react';
import { Download, Smartphone, Share } from 'lucide-react';

interface BeforeInstallPromptEvent extends Event {
  prompt: () => Promise<void>;
  userChoice: Promise<{ outcome: 'accepted' | 'dismissed' }>;
}

export default function AddToHomeScreen() {
  const [deferredPrompt, setDeferredPrompt] = useState<BeforeInstallPromptEvent | null>(null);
  const [showInstallButton, setShowInstallButton] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [isInstalled, setIsInstalled] = useState(false);
  const [isIOS, setIsIOS] = useState(false);

  useEffect(() => {
    // Detect iOS
    const detectIOS = () => {
      const userAgent = window.navigator.userAgent.toLowerCase();
      return /iphone|ipad|ipod/.test(userAgent);
    };

    // Check if app is already installed
    const checkInstalled = () => {
      // Check for standalone mode (PWA installed)
      if (window.matchMedia('(display-mode: standalone)').matches) {
        setIsInstalled(true);
        return;
      }
      
      // Check for iOS standalone
      const nav = window.navigator as Navigator & { standalone?: boolean };
      if (nav.standalone === true) {
        setIsInstalled(true);
        return;
      }
    };

    const ios = detectIOS();
    setIsIOS(ios);
    checkInstalled();

    // Listen for beforeinstallprompt event (Android Chrome)
    const handleBeforeInstallPrompt = (e: Event) => {
      e.preventDefault();
      setDeferredPrompt(e as BeforeInstallPromptEvent);
      // Show button immediately when prompt is ready
      setShowInstallButton(true);
    };

    // Listen for appinstalled event
    const handleAppInstalled = () => {
      setIsInstalled(true);
      setShowInstallButton(false);
      setShowModal(false);
      setDeferredPrompt(null);
    };

    // Listen for display mode changes
    const handleDisplayModeChange = () => {
      if (window.matchMedia('(display-mode: standalone)').matches) {
        setIsInstalled(true);
        setShowInstallButton(false);
      }
    };

    window.addEventListener('beforeinstallprompt', handleBeforeInstallPrompt);
    window.addEventListener('appinstalled', handleAppInstalled);
    window.matchMedia('(display-mode: standalone)').addEventListener('change', handleDisplayModeChange);

    return () => {
      window.removeEventListener('beforeinstallprompt', handleBeforeInstallPrompt);
      window.removeEventListener('appinstalled', handleAppInstalled);
      window.matchMedia('(display-mode: standalone)').removeEventListener('change', handleDisplayModeChange);
    };
  }, [isInstalled]);

  const handleInstallClick = () => {
    setShowModal(true);
  };

  const handleConfirmInstall = async () => {
    if (deferredPrompt) {
      // For Android Chrome and other browsers that support beforeinstallprompt
      deferredPrompt.prompt();
      const { outcome } = await deferredPrompt.userChoice;
      
      if (outcome === 'accepted') {
        console.log('User accepted the install prompt');
      } else {
        console.log('User dismissed the install prompt');
      }
      
      setDeferredPrompt(null);
    }
    
    setShowModal(false);
  };

  const handleCancelInstall = () => {
    setShowModal(false);
  };

  // Don't show if already installed
  if (isInstalled) {
    return null;
  }

  // Don't show if install button is not ready
  if (!showInstallButton) {
    return null;
  }

  return (
    <>
      {/* Install Button - Compact and positioned to avoid overlap */}
      <div className="fixed bottom-24 left-1/2 transform -translate-x-1/2 z-40">
        <button
          onClick={handleInstallClick}
          className="bg-gradient-to-r from-amber-500 to-yellow-500 hover:from-amber-600 hover:to-yellow-600 text-white px-4 py-2.5 rounded-xl shadow-lg hover:shadow-xl transition-all duration-300 transform hover:scale-105 flex items-center gap-2 font-semibold text-xs sm:text-sm"
        >
          <Download className="w-4 h-4" />
          Jadikan Aplikasi
        </button>
      </div>

      {/* Confirmation Modal */}
      {showModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-2xl p-6 max-w-sm w-full mx-4 transform transition-all duration-300 scale-100 max-h-[90vh] overflow-y-auto">
            <div className="text-center">
              <div className="w-16 h-16 bg-gradient-to-r from-amber-500 to-yellow-500 rounded-full flex items-center justify-center mx-auto mb-4">
                {isIOS ? <Share className="w-8 h-8 text-white" /> : <Smartphone className="w-8 h-8 text-white" />}
              </div>
              
              <h3 className="text-xl font-bold text-gray-900 mb-2">
                Jadikan Aplikasi
              </h3>
              
              {isIOS ? (
                <div className="text-left mb-6">
                  <p className="text-gray-600 mb-4 text-sm leading-relaxed">
                    Untuk menambahkan ke layar utama di iPhone/iPad:
                  </p>
                  <ol className="text-gray-600 text-sm space-y-2">
                    <li className="flex items-start gap-2">
                      <span className="bg-amber-500 text-white rounded-full w-5 h-5 flex items-center justify-center text-xs font-bold flex-shrink-0 mt-0.5">1</span>
                      <span>Tap tombol <strong>Bagikan</strong> di bawah</span>
                    </li>
                    <li className="flex items-start gap-2">
                      <span className="bg-amber-500 text-white rounded-full w-5 h-5 flex items-center justify-center text-xs font-bold flex-shrink-0 mt-0.5">2</span>
                      <span>Scroll ke bawah dan pilih <strong>&quot;Tambahkan ke Layar Utama&quot;</strong></span>
                    </li>
                    <li className="flex items-start gap-2">
                      <span className="bg-amber-500 text-white rounded-full w-5 h-5 flex items-center justify-center text-xs font-bold flex-shrink-0 mt-0.5">3</span>
                      <span>Tap <strong>&quot;Tambah&quot;</strong> untuk mengonfirmasi</span>
                    </li>
                  </ol>
                </div>
              ) : (
                <p className="text-gray-600 mb-6 text-sm leading-relaxed">
                  Apakah Anda ingin menjadikan halaman ini menjadi aplikasi? 
                  Aplikasi akan tersimpan di layar utama dengan ikon yang sama.
                </p>
              )}
              
              <div className="flex gap-3">
                <button
                  onClick={handleCancelInstall}
                  className="flex-1 px-4 py-2 border border-gray-300 text-gray-700 rounded-xl hover:bg-gray-50 transition-colors duration-200 font-medium"
                >
                  Batal
                </button>
                {isIOS ? (
                  <button
                    onClick={handleCancelInstall}
                    className="flex-1 px-4 py-2 bg-gradient-to-r from-amber-500 to-yellow-500 text-white rounded-xl hover:from-amber-600 hover:to-yellow-600 transition-all duration-200 font-medium"
                  >
                    Mengerti
                  </button>
                ) : (
                  <button
                    onClick={handleConfirmInstall}
                    className="flex-1 px-4 py-2 bg-gradient-to-r from-amber-500 to-yellow-500 text-white rounded-xl hover:from-amber-600 hover:to-yellow-600 transition-all duration-200 font-medium"
                  >
                    Ya, Install
                  </button>
                )}
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
}
