import { 
  collection, 
  getDocs
} from 'firebase/firestore';
import { db } from '../firebase';
import { RentBikeTypeProps, RentBikeListViewsProps } from '../types/bike';
import { Motorcycle } from '@/types';

export interface MotorTypeWithAvailability {
  id: string;
  name: string;
  type: string; // Dynamic type from Firebase
  pricePerDay: number;
  pricePerWeek: number;
  pricePerMonth: number;
  image: string | null;
  availableCount: number;
  totalCount: number;
  isAvailable: boolean;
  description?: string;
  color?: string; // Dynamic color
}

export interface MotorTypeFilter {
  id: string;
  name: string;
  displayName: string;
  color: string;
}

const BIKE_TYPE_COLLECTION = 'bike_type_list';
const BIKE_LIST_COLLECTION = 'bike_list';
const MERK_BIKE_COLLECTION = 'merk_bike';

// Dynamic color generation system
const COLOR_PALETTE = [
  'bg-red-500', 'bg-blue-500', 'bg-green-500', 'bg-yellow-500', 
  'bg-purple-500', 'bg-pink-500', 'bg-indigo-500', 'bg-orange-500',
  'bg-teal-500', 'bg-cyan-500', 'bg-lime-500', 'bg-amber-500',
  'bg-emerald-500', 'bg-violet-500', 'bg-rose-500', 'bg-sky-500'
];

// Generate consistent color for a given string
const generateColor = (str: string): string => {
  let hash = 0;
  for (let i = 0; i < str.length; i++) {
    const char = str.charCodeAt(i);
    hash = ((hash << 5) - hash) + char;
    hash = hash & hash; // Convert to 32bit integer
  }
  return COLOR_PALETTE[Math.abs(hash) % COLOR_PALETTE.length];
};

// No default images - will use placeholder component instead

export class MotorService {
  // Get all motor types with availability calculation
  static async getMotorTypesWithAvailability(): Promise<MotorTypeWithAvailability[]> {
    try {
      console.log('🔍 Fetching motor types from Firebase...');
      
      // Get all bike types
      const bikeTypesSnapshot = await getDocs(collection(db, BIKE_TYPE_COLLECTION));
      const bikeTypes: RentBikeTypeProps[] = [];
      
      console.log(`📊 Found ${bikeTypesSnapshot.size} bike types in collection`);
      
      bikeTypesSnapshot.forEach((doc) => {
        bikeTypes.push({
          id: doc.id,
          ...doc.data()
        } as RentBikeTypeProps);
      });

      // Get all bikes to calculate availability
      const bikesSnapshot = await getDocs(collection(db, BIKE_LIST_COLLECTION));
      const bikes: RentBikeListViewsProps[] = [];
      
      console.log(`🚲 Found ${bikesSnapshot.size} bikes in collection`);
      
      bikesSnapshot.forEach((doc) => {
        bikes.push({
          id: doc.id,
          ...doc.data()
        } as RentBikeListViewsProps);
      });

      // Group bikes by bike_type ID and calculate availability
      const bikeTypeCounts = new Map<string, { total: number; available: number }>();
      
      bikes.forEach(bike => {
        const bikeTypeId = bike.bike_type || ''; // This should be the bike_type ID reference
        const status = bike.status || '';
        
        if (!bikeTypeCounts.has(bikeTypeId)) {
          bikeTypeCounts.set(bikeTypeId, { total: 0, available: 0 });
        }
        
        const counts = bikeTypeCounts.get(bikeTypeId)!;
        counts.total++;
        
        // Consider bike available if status is 'available' or 'tersedia'
        if (status.toLowerCase() === 'available' || status.toLowerCase() === 'tersedia') {
          counts.available++;
        }
      });

      console.log('📈 Bike type counts:', Object.fromEntries(bikeTypeCounts));

      // Convert bike types to motor types with availability
      const motorTypes: MotorTypeWithAvailability[] = bikeTypes.map(bikeType => {
        const bikeTypeId = bikeType.id || '';
        const counts = bikeTypeCounts.get(bikeTypeId) || { total: 0, available: 0 };
        
        // Use bike merk directly as type - completely dynamic
        const motorType = (bikeType.bike_merk || 'unknown').toLowerCase();
        const color = generateColor(motorType);

        return {
          id: bikeType.id || '',
          name: bikeType.bike_type || 'Unknown Motor',
          type: motorType,
          pricePerDay: Number(bikeType.days) || 0,
          pricePerWeek: Number(bikeType.weeks) || 0,
          pricePerMonth: Number(bikeType.months) || 0,
          image: bikeType.image_url || null,
          availableCount: counts.available,
          totalCount: counts.total,
          isAvailable: counts.available > 0,
          description: `${bikeType.bike_type || 'Unknown Motor'} - ${counts.available} tersedia dari ${counts.total} total`,
          color: color
        };
      });

      console.log(`✅ Generated ${motorTypes.length} motor types`);
      
      // If no data from Firebase, return sample data for testing
      if (motorTypes.length === 0) {
        console.log('⚠️ No data from Firebase, returning sample data');
        const sampleTypes = ['honda', 'yamaha', 'listrik'];
        return sampleTypes.map((type, index) => ({
          id: `sample-${type}-${index + 1}`,
          name: `${type.charAt(0).toUpperCase() + type.slice(1)} Sample`,
          type: type,
          pricePerDay: 50000 + (index * 25000),
          pricePerWeek: 300000 + (index * 150000),
          pricePerMonth: 1000000 + (index * 500000),
          image: null,
          availableCount: 3 - index,
          totalCount: 5 - index,
          isAvailable: true,
          description: `${type.charAt(0).toUpperCase() + type.slice(1)} Sample - ${3 - index} tersedia dari ${5 - index} total`,
          color: generateColor(type)
        }));
      }
      
      // Filter out types with no bikes
      const filteredMotorTypes = motorTypes.filter(motor => motor.totalCount > 0);
      console.log(`🔍 Filtered to ${filteredMotorTypes.length} motor types with bikes`);
      
      return filteredMotorTypes;
      
    } catch (error) {
      console.error('❌ Error getting motor types with availability:', error);
      throw new Error('Failed to fetch motor types with availability');
    }
  }

  // Get motor types grouped by type (dynamic)
  static async getMotorTypesGrouped(): Promise<Record<string, MotorTypeWithAvailability[]>> {
    try {
      const motorTypes = await this.getMotorTypesWithAvailability();
      
      // Group by type dynamically
      const grouped: Record<string, MotorTypeWithAvailability[]> = {};
      motorTypes.forEach(motor => {
        if (!grouped[motor.type]) {
          grouped[motor.type] = [];
        }
        grouped[motor.type].push(motor);
      });
      
      return grouped;
    } catch (error) {
      console.error('Error getting grouped motor types:', error);
      throw new Error('Failed to fetch grouped motor types');
    }
  }

  // Get motor statistics
  static async getMotorStats(): Promise<{
    totalTypes: number;
    totalBikes: number;
    availableBikes: number;
    typeCounts: Record<string, number>;
  }> {
    try {
      const motorTypes = await this.getMotorTypesWithAvailability();
      
      // Calculate type counts dynamically
      const typeCounts: Record<string, number> = {};
      motorTypes.forEach(motor => {
        if (!typeCounts[motor.type]) {
          typeCounts[motor.type] = 0;
        }
        typeCounts[motor.type] += motor.availableCount;
      });
      
      const stats = {
        totalTypes: motorTypes.length,
        totalBikes: motorTypes.reduce((sum, motor) => sum + motor.totalCount, 0),
        availableBikes: motorTypes.reduce((sum, motor) => sum + motor.availableCount, 0),
        typeCounts: typeCounts
      };

      return stats;
    } catch (error) {
      console.error('Error getting motor stats:', error);
      throw new Error('Failed to fetch motor statistics');
    }
  }

  // Get motor type filters from merk_bike collection
  static async getMotorTypeFilters(): Promise<MotorTypeFilter[]> {
    try {
      console.log('🔍 Fetching motor type filters from merk_bike collection...');
      
      const merkBikeSnapshot = await getDocs(collection(db, MERK_BIKE_COLLECTION));
      const filters: MotorTypeFilter[] = [
        { id: 'all', name: 'all', displayName: 'Semua', color: 'bg-gray-500' }
      ]; // Always include 'all' option
      
      console.log(`📊 Found ${merkBikeSnapshot.size} motor brands in merk_bike collection`);
      
      merkBikeSnapshot.forEach((doc) => {
        const docData = doc.data();
        const brandId = doc.id;
        const brandName = docData.name || brandId;
        const brandType = brandName.toLowerCase();
        
        // Check if this type already exists
        const exists = filters.some(f => f.id === brandType);
        if (!exists) {
          filters.push({
            id: brandType,
            name: brandName,
            displayName: brandName.charAt(0).toUpperCase() + brandName.slice(1),
            color: generateColor(brandType)
          });
        }
      });

      console.log(`✅ Generated ${filters.length} motor type filters:`, filters);
      
      // If no data from Firebase, return default filters
      if (filters.length === 1) {
        console.log('⚠️ No brands found in merk_bike collection, returning default filters');
        return [
          { id: 'all', name: 'all', displayName: 'Semua', color: 'bg-gray-500' },
          { id: 'honda', name: 'honda', displayName: 'Honda', color: generateColor('honda') },
          { id: 'yamaha', name: 'yamaha', displayName: 'Yamaha', color: generateColor('yamaha') },
          { id: 'listrik', name: 'listrik', displayName: 'Listrik', color: generateColor('listrik') }
        ];
      }
      
      return filters;
      
    } catch (error) {
      console.error('❌ Error getting motor type filters:', error);
      // Return default filters as fallback
      return [
        { id: 'all', name: 'all', displayName: 'Semua', color: 'bg-gray-500' },
        { id: 'honda', name: 'honda', displayName: 'Honda', color: generateColor('honda') },
        { id: 'yamaha', name: 'yamaha', displayName: 'Yamaha', color: generateColor('yamaha') },
        { id: 'listrik', name: 'listrik', displayName: 'Listrik', color: generateColor('listrik') }
      ];
    }
  }

  // Convert MotorTypeWithAvailability to Motorcycle format for compatibility
  static convertToMotorcycle(motorType: MotorTypeWithAvailability): Motorcycle {
    return {
      id: motorType.id,
      name: motorType.name,
      isAvailable: motorType.isAvailable,
      pricePerDay: motorType.pricePerDay,
      pricePerWeek: motorType.pricePerWeek,
      pricePerMonth: motorType.pricePerMonth,
      status: motorType.isAvailable ? 'tersedia' : 'belum tersedia',
      type: motorType.type,
      image: motorType.image,
      description: motorType.description,
      color: motorType.color
    };
  }
}
