      <div className="absolute inset-0">
        <div className="absolute top-0 left-0 w-full h-full opacity-5">
          <div className="absolute top-20 right-10 w-32 h-32 border border-amber-300 rounded-full"></div>
          <div className="absolute bottom-20 left-10 w-24 h-24 border border-yellow-300 rounded-full"></div>
          <div className="absolute top-1/2 left-1/4 w-16 h-16 border border-orange-300 rounded-full"></div>
        </div>
      </div>

