'use client';

import { useEffect, useState, useRef, useCallback } from 'react';
import Image from 'next/image';
import { testimonials } from '@/lib/data';
import { 
  Star, 
  Quote, 
  Users, 
  Heart, 
  ThumbsUp, 
  Award,
  Sparkles,
  CheckCircle,
  ArrowLeft,
  ArrowRight
} from 'lucide-react';
import { getYearsText } from '../../../utils';

export default function TestimonialsComponent() {
  const [isVisible, setIsVisible] = useState(false);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isAutoScrolling, setIsAutoScrolling] = useState(true);
  const scrollContainerRef = useRef<HTMLDivElement>(null);
  const autoScrollIntervalRef = useRef<NodeJS.Timeout | null>(null);
  const touchStartXRef = useRef<number>(0);
  const touchEndXRef = useRef<number>(0);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
        }
      },
      { threshold: 0.1 }
    );

    const element = document.getElementById('testimonials');
    if (element) {
      observer.observe(element);
    }

    return () => observer.disconnect();
  }, []);

  // Auto-scroll functionality
  const startAutoScroll = useCallback(() => {
    if (autoScrollIntervalRef.current) {
      clearInterval(autoScrollIntervalRef.current);
    }
    autoScrollIntervalRef.current = setInterval(() => {
      if (isAutoScrolling) {
        setCurrentIndex((prev) => (prev + 1) % testimonials.length);
      }
    }, 4000);
  }, [isAutoScrolling, testimonials.length]);

  const stopAutoScroll = useCallback(() => {
    if (autoScrollIntervalRef.current) {
      clearInterval(autoScrollIntervalRef.current);
      autoScrollIntervalRef.current = null;
    }
  }, []);

  const nextTestimonial = useCallback(() => {
    setCurrentIndex((prev) => (prev + 1) % testimonials.length);
    stopAutoScroll();
    setTimeout(() => {
      if (isAutoScrolling) startAutoScroll();
    }, 5000);
  }, [testimonials.length, isAutoScrolling, startAutoScroll, stopAutoScroll]);

  const prevTestimonial = useCallback(() => {
    setCurrentIndex((prev) => (prev - 1 + testimonials.length) % testimonials.length);
    stopAutoScroll();
    setTimeout(() => {
      if (isAutoScrolling) startAutoScroll();
    }, 5000);
  }, [testimonials.length, isAutoScrolling, startAutoScroll, stopAutoScroll]);

  // Touch handlers for swipe gestures
  const handleTouchStart = (e: React.TouchEvent) => {
    touchStartXRef.current = e.touches[0].clientX;
    setIsAutoScrolling(false);
    stopAutoScroll();
  };

  const handleTouchMove = (e: React.TouchEvent) => {
    touchEndXRef.current = e.touches[0].clientX;
  };

  const handleTouchEnd = () => {
    if (!touchStartXRef.current || !touchEndXRef.current) return;
    
    const distance = touchStartXRef.current - touchEndXRef.current;
    const isLeftSwipe = distance > 50;
    const isRightSwipe = distance < -50;

    if (isLeftSwipe) {
      nextTestimonial();
    } else if (isRightSwipe) {
      prevTestimonial();
    }

    setTimeout(() => {
      setIsAutoScrolling(true);
      startAutoScroll();
    }, 3000);
  };

  // Scroll to specific testimonial
  const scrollToTestimonial = useCallback((index: number) => {
    if (scrollContainerRef.current) {
      const container = scrollContainerRef.current;
      const cardWidth = container.scrollWidth / testimonials.length;
      container.scrollTo({
        left: cardWidth * index,
        behavior: 'smooth'
      });
    }
  }, [testimonials.length]);

  // Effect for auto-scroll
  useEffect(() => {
    if (isVisible && isAutoScrolling) {
      startAutoScroll();
    }
    return () => stopAutoScroll();
  }, [isVisible, isAutoScrolling, startAutoScroll, stopAutoScroll]);

  // Effect to sync scroll position with currentIndex
  useEffect(() => {
    scrollToTestimonial(currentIndex);
  }, [currentIndex, scrollToTestimonial]);

  // Cleanup on unmount
  useEffect(() => {
    return () => {
      if (autoScrollIntervalRef.current) {
        clearInterval(autoScrollIntervalRef.current);
      }
    };
  }, []);

  const renderStars = (rating: number) => {
    return Array.from({ length: 5 }, (_, index) => (
      <Star
        key={index}
        className={`w-5 h-5 ${
          index < rating ? 'text-yellow-400 fill-current' : 'text-gray-300'
        }`}
      />
    ));
  };

  return (
    <section 
      id="testimonials" 
      className="py-16 sm:py-20 lg:py-28 bg-gradient-to-br from-white via-amber-50/20 to-yellow-50/20 relative overflow-hidden"
    >
      {/* Background Elements */}
      <div className="absolute inset-0">
        <div className="absolute top-0 left-0 w-full h-full opacity-5">
          <div className="absolute top-20 left-10 w-32 h-32 border border-amber-300 rounded-full"></div>
          <div className="absolute bottom-20 right-10 w-24 h-24 border border-yellow-300 rounded-full"></div>
          <div className="absolute top-1/2 right-1/4 w-16 h-16 border border-orange-300 rounded-full"></div>
        </div>
      </div>

      <div className="container mx-auto px-4 sm:px-6 lg:px-8 relative z-10">
        
        {/* Section Header */}
        <div className={`text-center mb-12 sm:mb-16 lg:mb-20 transition-all duration-1000 ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-8'}`}>
          <div className="inline-flex items-center gap-2 px-4 py-2 bg-gradient-to-r from-amber-100 to-yellow-100 border border-amber-200 rounded-full mb-6">
            <Heart className="w-4 h-4 text-amber-600" />
            <span className="text-sm font-semibold text-amber-700">Testimoni</span>
            <Star className="w-4 h-4 text-yellow-500" />
          </div>
          
          <h2 className="text-3xl sm:text-4xl lg:text-5xl xl:text-6xl font-black text-gray-900 mb-6">
            <span className="bg-gradient-to-r from-amber-600 via-yellow-600 to-orange-600 bg-clip-text text-transparent">
              Testimoni
            </span>
            <br />
            <span className="text-gray-800">Pelanggan</span>
          </h2>
          
          <p className="text-lg sm:text-xl lg:text-2xl text-gray-600 max-w-3xl mx-auto leading-relaxed">
            Dengarkan pengalaman pelanggan kami yang telah menggunakan layanan Andi Rental
          </p>
        </div>

        {/* Stats Overview */}
        <div className={`grid grid-cols-2 lg:grid-cols-4 gap-4 sm:gap-6 mb-12 sm:mb-16 transition-all duration-1000 delay-200 ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-8'}`}>
          {[
            { number: '1000+', label: 'Pelanggan Puas', icon: Users },
            { number: '4.9', label: 'Rating Rata-rata', icon: Star },
            { number: '98%', label: 'Kepuasan', icon: ThumbsUp },
            { number: getYearsText(), label: 'Tahun Terpercaya', icon: Award }
          ].map((stat, index) => (
            <div
              key={index}
              className="group relative bg-white/80 backdrop-blur-sm border border-amber-100 rounded-2xl p-6 sm:p-8 hover:bg-white hover:shadow-xl transition-all duration-500 transform hover:scale-105 hover:-translate-y-2"
            >
              <div className="absolute inset-0 bg-gradient-to-r from-amber-400 via-yellow-400 to-orange-400 opacity-0 group-hover:opacity-5 transition-opacity duration-500 rounded-2xl"></div>
              
              <div className="relative z-10 text-center">
                <div className="inline-flex items-center justify-center w-16 h-16 bg-gradient-to-r from-amber-100 to-yellow-100 rounded-2xl mb-4 group-hover:scale-110 transition-transform duration-300">
                  <stat.icon className="w-8 h-8 text-amber-600" />
                </div>
                <div className="text-2xl sm:text-3xl lg:text-4xl font-black text-amber-600 mb-2">
                  {stat.number}
                </div>
                <div className="text-sm sm:text-base text-gray-600 font-medium">
                  {stat.label}
                </div>
              </div>
            </div>
          ))}
        </div>

        {/* Featured Testimonial */}
        <div className={`mb-12 sm:mb-16 transition-all duration-1000 delay-400 ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-8'}`}>
          <div className="relative bg-gradient-to-r from-amber-500 via-yellow-500 to-orange-500 rounded-3xl p-8 sm:p-12 lg:p-16 shadow-2xl overflow-hidden">
            {/* Background Pattern */}
            <div className="absolute inset-0 opacity-10">
              <div className="absolute top-0 right-0 w-32 h-32 border border-white rounded-full"></div>
              <div className="absolute bottom-0 left-0 w-24 h-24 border border-white rounded-full"></div>
            </div>
            
            <div className="relative z-10 text-center">
              <Quote className="w-12 h-12 text-white/80 mx-auto mb-6" />
              
              <div className="flex justify-center mb-6">
                {renderStars(5)}
              </div>
              
              <blockquote className="text-white text-lg sm:text-xl lg:text-2xl font-medium mb-8 sm:mb-10 max-w-4xl mx-auto leading-relaxed">
                &ldquo;Pelayanan sangat memuaskan! Motor dalam kondisi prima, harga terjangkau, dan proses booking yang mudah. 
                Sudah beberapa kali sewa dan selalu puas dengan layanan Andi Rental.&rdquo;
              </blockquote>
              
              <div className="flex items-center justify-center gap-4">
                <div className="w-16 h-16 rounded-full overflow-hidden border-4 border-white/30">
                  <Image
                    src={testimonials[0].image}
                    alt="Pelanggan Puas"
                    width={64}
                    height={64}
                    className="w-full h-full object-cover"
                  />
                </div>
                <div className="text-left">
                  <div className="text-white font-bold text-lg">Ahmad Sulaiman</div>
                  <div className="text-amber-100 text-sm">Pelanggan Setia</div>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Testimonials Section */}
        <div className={`mb-12 sm:mb-16 transition-all duration-1000 delay-600 ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-8'}`}>
          
          {/* Desktop Grid - Hidden on Mobile */}
          <div className="hidden md:grid md:grid-cols-2 lg:grid-cols-3 gap-6 sm:gap-8">
            {testimonials.slice(1, 7).map((testimonial, index) => (
              <div
                key={testimonial.id}
                className="group relative bg-white/80 backdrop-blur-sm border border-amber-100 rounded-2xl p-6 sm:p-8 hover:bg-white hover:shadow-xl transition-all duration-500 transform hover:scale-105 hover:-translate-y-2"
                style={{ animationDelay: `${index * 0.1}s` }}
              >
                <div className="absolute inset-0 bg-gradient-to-r from-amber-400 via-yellow-400 to-orange-400 opacity-0 group-hover:opacity-5 transition-opacity duration-500 rounded-2xl"></div>
                
                <div className="relative z-10">
                  {/* Rating */}
                  <div className="flex mb-4">
                    {renderStars(testimonial.rating)}
                  </div>
                  
                  {/* Quote */}
                  <Quote className="w-8 h-8 text-amber-400 mb-4" />
                  
                  {/* Content */}
                  <p className="text-gray-700 leading-relaxed text-sm sm:text-base mb-6">
                    &ldquo;Motor berkualitas, pelayanan ramah, dan harga terjangkau. 
                    Sangat direkomendasikan untuk kebutuhan transportasi di Makassar!&rdquo;
                  </p>
                  
                  {/* Author */}
                  <div className="flex items-center gap-3">
                    <div className="w-12 h-12 rounded-full overflow-hidden border-2 border-amber-200">
                      <Image
                        src={testimonial.image}
                        alt={testimonial.name}
                        width={48}
                        height={48}
                        className="w-full h-full object-cover"
                      />
                    </div>
                    <div>
                      <div className="font-bold text-gray-900">{testimonial.name}</div>
                      <div className="text-sm text-gray-600">Pelanggan Puas</div>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>

          {/* Mobile Horizontal Scroll - Visible on Mobile Only */}
          <div className="md:hidden relative">
            {/* Navigation Arrows */}
            <div className="flex justify-between items-center mb-6">
              <button
                onClick={prevTestimonial}
                className="flex items-center justify-center w-12 h-12 bg-white/80 backdrop-blur-sm border border-amber-200 rounded-full shadow-lg hover:bg-white hover:shadow-xl transition-all duration-300 transform hover:scale-110"
                aria-label="Testimoni sebelumnya"
              >
                <ArrowLeft className="w-5 h-5 text-amber-600" />
              </button>
              
              <div className="text-center">
                <span className="text-sm text-gray-600 font-medium">
                  {currentIndex + 1} dari {testimonials.length}
                </span>
              </div>
              
              <button
                onClick={nextTestimonial}
                className="flex items-center justify-center w-12 h-12 bg-white/80 backdrop-blur-sm border border-amber-200 rounded-full shadow-lg hover:bg-white hover:shadow-xl transition-all duration-300 transform hover:scale-110"
                aria-label="Testimoni selanjutnya"
              >
                <ArrowRight className="w-5 h-5 text-amber-600" />
              </button>
            </div>

            {/* Scrollable Container */}
            <div 
              ref={scrollContainerRef}
              className="flex gap-4 overflow-x-auto scrollbar-hide snap-x snap-mandatory pb-4"
              style={{ scrollbarWidth: 'none', msOverflowStyle: 'none' }}
              onTouchStart={handleTouchStart}
              onTouchMove={handleTouchMove}
              onTouchEnd={handleTouchEnd}
              onMouseEnter={() => setIsAutoScrolling(false)}
              onMouseLeave={() => setIsAutoScrolling(true)}
            >
              {testimonials.map((testimonial, index) => (
                <div
                  key={testimonial.id}
                  className="flex-none w-80 snap-center group relative bg-white/80 backdrop-blur-sm border border-amber-100 rounded-2xl p-6 hover:bg-white hover:shadow-xl transition-all duration-500 transform hover:scale-105"
                >
                  <div className="absolute inset-0 bg-gradient-to-r from-amber-400 via-yellow-400 to-orange-400 opacity-0 group-hover:opacity-5 transition-opacity duration-500 rounded-2xl"></div>
                  
                  <div className="relative z-10">
                    {/* Rating */}
                    <div className="flex mb-4">
                      {renderStars(testimonial.rating)}
                    </div>
                    
                    {/* Quote */}
                    <Quote className="w-8 h-8 text-amber-400 mb-4" />
                    
                    {/* Content */}
                    <p className="text-gray-700 leading-relaxed text-sm mb-6">
                      &ldquo;Motor berkualitas, pelayanan ramah, dan harga terjangkau. 
                      Sangat direkomendasikan untuk kebutuhan transportasi di Makassar!&rdquo;
                    </p>
                    
                    {/* Author */}
                    <div className="flex items-center gap-3">
                      <div className="w-12 h-12 rounded-full overflow-hidden border-2 border-amber-200">
                        <Image
                          src={testimonial.image}
                          alt={testimonial.name}
                          width={48}
                          height={48}
                          className="w-full h-full object-cover"
                        />
                      </div>
                      <div>
                        <div className="font-bold text-gray-900">{testimonial.name}</div>
                        <div className="text-sm text-gray-600">Pelanggan Puas</div>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>

            {/* Dots Indicator */}
            <div className="flex justify-center gap-2 mt-6">
              {testimonials.map((_, index) => (
                <button
                  key={index}
                  onClick={() => setCurrentIndex(index)}
                  className={`w-2 h-2 rounded-full transition-all duration-300 ${
                    index === currentIndex 
                      ? 'bg-amber-500 w-6' 
                      : 'bg-amber-200 hover:bg-amber-300'
                  }`}
                  aria-label={`Lihat testimoni ${index + 1}`}
                />
              ))}
            </div>
          </div>
        </div>

        {/* CTA Section */}
        <div className={`text-center transition-all duration-1000 delay-800 ${isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-8'}`}>
          <div className="bg-gradient-to-r from-amber-50 to-yellow-50 border border-amber-200 rounded-2xl p-8 sm:p-12">
            <div className="flex items-center justify-center gap-3 mb-6">
              <div className="w-12 h-12 bg-gradient-to-r from-amber-500 to-yellow-500 rounded-xl flex items-center justify-center">
                <CheckCircle className="w-6 h-6 text-white" />
              </div>
              <h3 className="text-2xl sm:text-3xl font-black text-amber-900">
                Bergabung dengan 1000+ Pelanggan Puas
              </h3>
            </div>
            
            <p className="text-amber-800 text-lg sm:text-xl mb-8 max-w-2xl mx-auto leading-relaxed">
              Jangan ragu untuk mencoba layanan kami. Dapatkan pengalaman terbaik 
              dengan motor berkualitas dan pelayanan profesional.
            </p>
            
            <div className="flex flex-col sm:flex-row gap-4 justify-center">
              <a
                href="https://wa.me/6282343464689"
                target="_blank"
                rel="noopener noreferrer"
                className="group inline-flex items-center justify-center px-8 sm:px-10 py-4 sm:py-5 bg-gradient-to-r from-green-500 to-green-600 text-white font-bold rounded-2xl hover:from-green-600 hover:to-green-700 transition-all duration-300 transform hover:scale-105 shadow-lg hover:shadow-xl"
              >
                <svg className="w-5 h-5 mr-2 group-hover:scale-110 transition-transform duration-300" fill="currentColor" viewBox="0 0 24 24">
                  <path d="M17.472 14.382c-.297-.149-1.758-.867-2.03-.967-.273-.099-.471-.148-.67.15-.197.297-.767.966-.94 1.164-.173.199-.347.223-.644.075-.297-.15-1.255-.463-2.39-1.475-.883-.788-1.48-1.761-1.653-2.059-.173-.297-.018-.458.13-.606.134-.133.298-.347.446-.52.149-.174.198-.298.298-.497.099-.198.05-.371-.025-.52-.075-.149-.669-1.612-.916-2.207-.242-.579-.487-.5-.669-.51-.173-.008-.371-.01-.57-.01-.198 0-.52.074-.792.372-.272.297-1.04 1.016-1.04 2.479 0 1.462 1.065 2.875 1.213 3.074.149.198 2.096 3.2 5.077 4.487.709.306 1.262.489 1.694.625.712.227 1.36.195 1.871.118.571-.085 1.758-.719 2.006-1.413.248-.694.248-1.289.173-1.413-.074-.124-.272-.198-.57-.347m-5.421 7.403h-.004a9.87 9.87 0 01-5.031-1.378l-.361-.214-3.741.982.998-3.648-.235-.374a9.86 9.86 0 01-1.51-5.26c.001-5.45 4.436-9.884 9.888-9.884 2.64 0 5.122 1.03 6.988 2.898a9.825 9.825 0 012.893 6.994c-.003 5.45-4.437 9.884-9.885 9.884m8.413-18.297A11.815 11.815 0 0012.05 0C5.495 0 .16 5.335.157 11.892c0 2.096.547 4.142 1.588 5.945L.057 24l6.305-1.654a11.882 11.882 0 005.683 1.448h.005c6.554 0 11.89-5.335 11.893-11.893A11.821 11.821 0 0020.885 3.488" />
                </svg>
                Sewa Motor Sekarang
              </a>
              
              <button
                onClick={() => {
                  const element = document.querySelector('#listmotor');
                  if (element) {
                    element.scrollIntoView({ behavior: 'smooth' });
                  }
                }}
                className="group inline-flex items-center justify-center px-8 sm:px-10 py-4 sm:py-5 border-2 border-amber-200 text-amber-700 font-bold rounded-2xl hover:border-amber-400 hover:text-amber-800 transition-all duration-300 transform hover:scale-105 bg-white/50 hover:bg-amber-50 backdrop-blur-sm"
              >
                <Sparkles className="w-5 h-5 mr-2 group-hover:scale-110 transition-transform duration-300" />
                Lihat Motor
              </button>
            </div>
        </div>
        </div>
      </div>
    </section>
  );
}
